
import { defineComponent } from 'vue';

export default defineComponent({
  name: "PinChar",

  props: {
    value: {
      type: String,
      default: ''
    },

    isVisible: {
      type: Boolean,
      default: true
    },

    isDisabled: {
      type: Boolean,
      default: false
    }
  },

  computed: {
    isValue() {
      return this.value.length;
    }
  }
});
